.modal {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: #fff;
    padding: 40px 16px;
    z-index: 999;

    &__title {
        font-size: 21px;
        span {
            font-weight: 400;
        }
    }
    .price {
        margin-top: 20px;
    }
    .discount {
        margin: 14px 0;
        padding-bottom: 14px;
    }
    .amount {
        padding: 14px 0;
        border-top: 1px solid #E5E5E5;
        border-bottom: 1px solid #E5E5E5;
    }
    .price,
    .discount,
    .amount {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .text {
        font-weight: 400;
        font-size: 17px;     
        color: #333333;
    }
    .summary {
        font-weight: 700;
        font-size: 17px;
        color: #333333;
    }

    &__footer {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 85px;
        border-top: 1px solid #D6D6D6;
        background: linear-gradient(0deg, #FFFFFF, #FFFFFF), #F8F8F8;
    }
    &__close {
        max-width: 328px;
        width: 100%;
        padding: 16px 20px;
        font-weight: 500;
        font-size: 17px;
        letter-spacing: 0.5px;

        color: #333333;
        background: #FFFFFF;
        border: 1px solid #D6D6D6;
        box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.12), 0px 3px 1px rgba(0, 0, 0, 0.04), 0px 2px 0px rgba(149, 135, 128, 0.4);
        border-radius: 4px;
    }
}